<template>
    <div>
        <v-chart :option="option" style="height: 400px"></v-chart>
    </div>
</template>

<script setup>
import { use } from 'echarts/core'
import { LineChart } from 'echarts/charts'
import { GridComponent } from 'echarts/components'
import { CanvasRenderer } from 'echarts/renderers'
import { LegendComponent } from 'echarts/components';
import { DataZoomComponent } from 'echarts/components';
import { TooltipComponent } from 'echarts/components';
import VChart from 'vue-echarts';
import { ref, reactive } from 'vue'
import EventBus from "../common/event-bus"

use([GridComponent, LineChart, CanvasRenderer, LegendComponent, DataZoomComponent, TooltipComponent])


const option = reactive({
    animation: false,
    grid: {
        top: 40,
        left: 50,
        right: 40,
        bottom: 50
    },
    tooltip: {
        trigger: 'axis',
    },
    legend: {
        orient: 'vertical',
        right: 30,
        top: 35,
    },
    xAxis: {
        name: '角度',
        min: 0,
        max: 180,
        minorTick: {
            show: true
        },
        minorSplitLine: {
            show: true
        }
    },
    yAxis: {
        name: '应力',
        minorTick: {
            show: true
        },
        minorSplitLine: {
            show: true
        }
    },
    series: [
        {
            type: 'line',
            showSymbol: false,
            clip: true,
            name: '正应力',
            data: []
        },
        {
            type: 'line',
            showSymbol: false,
            clip: true,
            name: '剪应力',
            data: []
        }
    ]
});

let sigmaX = ref

EventBus.on('p-click', (val) => {

    const { sigmaX, sigmaY, tauX } = val
    const sigma_alph = []
    const tau_alph = []

    for (let i = 0; i <= 180; i++) {
        const rad = i * Math.PI / 180
        const sigmaA = (sigmaX + sigmaY) / 2 + (sigmaX - sigmaY) / 2 * Math.cos(2 * rad) - tauX * Math.sin(2 * rad)
        const tauA = (sigmaX - sigmaY) / 2 * Math.sin(2 * rad) + tauX * Math.cos(2 * rad)
        sigma_alph.push([i, sigmaA])
        tau_alph.push([i, tauA])
    }


    option.series[0].data = sigma_alph
    option.series[1].data = tau_alph
})

</script>

<style scoped></style>