<template>
    <div class="container">
        <a-row>
            <a-col :span="24">
                <div class="centerDiv">
                    <a-image width="350" src="./images/morcercle.png" />
                </div>
            </a-col>
            <a-col>
                <div class="centerDiv">
                    <div class="imageTitle">图1. 两轴应力状态</div>
                </div>
            </a-col>
            <a-col>
                <a-form :model="form" style="padding-top: 20px;">
                    <a-row class="rowForm">
                        <a-col :span="24">
                            <div class="twoDiv">
                                <vue-mathjax :formula="sigmaX" />
                                <a-input-number v-model="form.sigmaX" placeholder="Please Enter" />
                            </div>
                        </a-col>
                    </a-row>
                    <a-row class="rowForm">
                        <a-col :span="24">
                            <div class="twoDiv">
                                <vue-mathjax :formula="sigmaY" />
                                <a-input-number v-model="form.sigmaY" placeholder="Please Enter" />
                            </div>
                        </a-col>
                    </a-row>
                    <a-row class="rowForm">
                        <a-col :span="24">
                            <div class="twoDiv">
                                <vue-mathjax :formula="tauX" />
                                <a-input-number v-model="form.tauX" placeholder="Please Enter" />
                            </div>
                        </a-col>
                    </a-row>
                    <a-row>
                        <a-col :span="24">
                            <div class="centerDiv" style="padding-top: 10px;">
                                <a-button type="primary" @click="onClick">一键计算</a-button>
                            </div>
                        </a-col>
                    </a-row>
                </a-form>
            </a-col>
        </a-row>

        <a-modal v-model:visible="visible" @ok="handleClick" @cancel="handleClick">
            <template #title>
                错误
            </template>
            <div>请输入应力值</div>
        </a-modal>
    </div>
</template>

<script>
import EventBus from "../common/event-bus"

export default {
    methods: {
        onClick() {
            if (this.form.sigmaX === 0 && this.form.sigmaY === 0 && this.form.tauX === 0) {
                this.visible = true
                return
            }

            const sigmaX =  this.form.sigmaX
            const sigmaY =  this.form.sigmaY
            const tauX =  this.form.tauX
            EventBus.emit("p-click", {
                sigmaX,
                sigmaY,
                tauX
            });
        },

        handleClick() {
            this.visible = true;
        }
    },
    data() {
        const form = {
            sigmaX: 0,
            sigmaY: 0,
            tauX: 0,
        }
        return {
            sigmaX: '$\\sigma_{x}$',
            sigmaY: '$\\sigma_{y}$',
            tauX: '$\\tau_{x}$',
            tauY: '$\\tau_{y}$',
            form,
            visible: false
        }
    }
}

</script>

<style scoped>
.container {
    border-right: 8px solid rgb(187, 186, 186);
}

.centerDiv {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    justify-items: center;
}

.rowForm {
    padding: 5px;
}

.twoDiv {
    display: grid;
    grid-template-columns: 1fr 3fr;
    grid-template-rows: 1fr;
    justify-items: center;
    justify-content: center;
    padding-right: 10px;
}

.mainCard {
    width: 800px;
    margin: 10px;
}


.formDiv {
    /* height: 300px; */
    padding: 10px;
    padding-left: 10px;
    /* box-shadow: rgba(0, 0, 0, 0.3) 0px 5px 20px, rgba(0, 0, 0, 0.22) 0px 15px 12px; */
}
</style>