<template>
    <a-row>
        <a-col :span="24">
            <div class="formula">
                <vue-mathjax :formula="mainStreeForm" />
            </div>
        </a-col>
        <a-col :span="24">
            <div class="formula">
                <vue-mathjax :formula="alphaForm" />
            </div>
        </a-col>
    </a-row>
</template>

<script setup>
import EventBus from "../common/event-bus"
import { ref } from 'vue'
const mainStreeOrigin = `\\left.\\begin{matrix}
\\sigma_{1}
 \\\\
\\sigma_{2}
\\end{matrix}\\right\\} = \\frac{\\sigma_x+\\sigma_y}{2}\\pm \\sqrt{\\left (\\frac{\\sigma_x-\\sigma_y}{2}  \\right )^2+\\tau _{x}^{2} }`
const alphaOrigin = '\\alpha_{0} = -\\frac{1}{2} arctan\\frac{2\\tau_x}{\\sigma_{x} - \\sigma_{y}}'
const mainStreeForm = ref('$$' + mainStreeOrigin + '$$')
const alphaForm = ref(`$$${alphaOrigin}$$`)

EventBus.on('p-click', (val) => {
    // mainStreeForm.value = `$123$`
    const { sigmaX, sigmaY, tauX } = val
    const sigma1 = (sigmaX + sigmaY) / 2 + Math.sqrt(Math.pow((sigmaX - sigmaY) / 2, 2) + tauX * tauX)
    const sigma2 = (sigmaX + sigmaY) / 2 - Math.sqrt(Math.pow((sigmaX - sigmaY) / 2, 2) + tauX * tauX)
    mainStreeForm.value = '$$' + mainStreeOrigin +
        `= \\left\\{\\begin{matrix}
${sigma1.toFixed(2)}
 \\\\
${sigma2.toFixed(2)}
\\end{matrix}\\right.`
        + '$$'

    let alpha0 = - 0.5 * Math.atan(2 * tauX / (sigmaX - sigmaY))
    if (Math.abs(alpha0) < 1E-10) {
        alpha0 = 0
    }
    const rad = (alpha0 * (180 / Math.PI)).toFixed(2)
    alphaForm.value = '$$' + alphaOrigin + `=${rad}` + '°$$'

})


</script>

<style scoped>
.formula>span {
    float: left;
    margin-bottom: 20px;
    margin-left: 20px;
}
</style>