<template>
    <a-row>
        <a-col :span="24">
            <div class="formula">
                <vue-mathjax :formula="mainStreeForm" />
            </div>
        </a-col>
        <a-col :span="24">
            <div class="formula">
                <vue-mathjax :formula="alphaForm" />
            </div>
        </a-col>
        <a-col :span="24">
            <div class="formula">
                <vue-mathjax :formula="tauMaxForm" />
            </div>
        </a-col>
        <a-col :span="24">
            <div class="formula">
                <vue-mathjax :formula="tauBetaForm" />
            </div>
        </a-col>
    </a-row>
</template>

<script setup>

import EventBus from "../common/event-bus"
import { ref } from 'vue'

const tauMaxOri = `\\left.\\begin{matrix}
\\tau_{max}
 \\\\
\\tau_{min}
\\end{matrix}\\right\\} = \\pm \\sqrt{\\left ( \\frac{\\sigma_x - \\sigma_y}{2}  \\right )^2 + \\tau_{x}^{2} }`
const betaOri = `\\beta =\\frac{1}{2}arctan\\frac{\\sigma_x - \\sigma_y}{2\tau_x}`

const mainStreeForm = ref(`$$\\sigma_{\\alpha} = \\frac{\\sigma_x + \\sigma_y}{2} + \\frac{\\sigma_x - \\sigma_y }{2}cos2\\alpha -\\tau_xsin2\\alpha$$`)
const alphaForm = ref(`$$\\tau_{\\alpha} = \\frac{\\sigma_{x} - \\sigma_{y}}{2}sin2\\alpha + \\tau_{x}cos2\\alpha$$`)
const tauMaxForm = ref(`$$${tauMaxOri}$$`)
const tauBetaForm = ref(`$$${betaOri}$$`)

EventBus.on('p-click', (val) => {
    const { sigmaX, sigmaY, tauX } = val
    const tauMax = Math.sqrt(Math.pow((sigmaX - sigmaY) / 2, 2) + tauX * tauX).toFixed(2)
    const tauMin = -tauMax
    const beta = 0.5 * Math.atan((sigmaX - sigmaY) / (2 * tauX)) * 180 / Math.PI
    tauMaxForm.value = `$$${tauMaxOri} = \\left\\{\\begin{matrix}
${tauMax}
 \\\\
${tauMin}
\\end{matrix}\\right. $$`
    tauBetaForm.value = `$$${betaOri}=${beta.toFixed(2)}°$$`

})

</script>

<style scoped>
.formula>span {
    float: left;
    margin-bottom: 20px;
    margin-left: 20px;
}
</style>